import React,{ Component } from 'react';
import { Editor, EditorState, RichUtils, ContentState, convertToRaw } from 'draft-js';
import draftToHtml from 'draftjs-to-html';
import { connect } from 'react-redux';
import htmlToDraft from 'html-to-draftjs';
import Link from 'next/link';
import NoSSR from 'react-no-ssr';
class EditArticle1 extends Component {
  title='';
  content='';
  constructor(props) {
    super(props)
    const { selectedArticle}= this.props;
    this.title = selectedArticle.title;
    this.content = selectedArticle.content;
    
    const contentBlock = htmlToDraft(this.title);
    const contentBlock1 = htmlToDraft(this.content);
    
    if(contentBlock||contentBlock1) {
      const contentState = ContentState.createFromBlockArray(contentBlock.contentBlocks);
      const editorState = EditorState.createWithContent(contentState);
      const contentState1 = ContentState.createFromBlockArray(contentBlock1.contentBlocks);
      const editorState1 = EditorState.createWithContent(contentState1);
      this.state = {
        editorState,editorState1
      }

    }
   
  }

  onSave = (id) => {
    console.log(id);
    
  };

  onEditorStateChange = (editorState) => {
    this.setState({
      editorState
    });
  };
  onEditorStateChange1 = (editorState1) => {
    this.setState({
      editorState1
    });
  };

  onChange = (editorState) => {
    this.setState({
      editorState 
    }) 
  }

  handleKeyCommand = (command) => {
    const newState = RichUtils.handleKeyCommand(this.state.editorState, command);
    if (newState) {
      this.onChange(newState);
      return 'handled';
    }
    return 'not-handled';
  }

  onUnderlineClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'UNDERLINE'));
  }

  onBoldClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'BOLD'))
  }

  onItalicClick = () => {
    this.onChange(RichUtils.toggleInlineStyle(this.state.editorState, 'ITALIC'))
  }



  render() {
    const { selectedArticle}= this.props;
    const { editorState,editorState1 } = this.state;

    return(
      <div className="article-content App">
        <div className="page-title">
        <Link href='/'><h2>&lt; home</h2></Link>
              <h1 className="title">Articles</h1>
              <hr/>
            </div>
        
          <Editor 
            editorState={editorState}
            wrapperClassName="title__wrapper"
            editorClassName="demo-editor"
            onChange={this.onEditorStateChange}
            />
            <hr/>
            <div className="editorContainer">
          <button onClick={this.onUnderlineClick}>U</button>
          <button onClick={this.onBoldClick}><b>B</b></button>
          <button onClick={this.onItalicClick}><em>I</em></button>        
          <div className="editors">
            <Editor 
            editorState={editorState1}
            wrapperClassName="title__wrapper"
            editorClassName="demo-editor"
            onChange={this.onEditorStateChange1}
            />
            </div>
            <button onClick={()=> this.onSave(draftToHtml(convertToRaw(editorState.getCurrentContent())))}>save</button>
        </div>

        <style jsx>
        {`
        .demo-wrapper {
          width: 100% !important;
          display: block !important;
          margin-bottom: 25px !important;
          height: 400px !important;
        }
        
        .rdw-editor-toolbar {
          padding: 6px 5px 0;
          border-radius: 2px;
          border: 1px solid #F1F1F1;
          display: flex;
          justify-content: flex-start;
          background: white;
          flex-wrap: wrap;
          font-size: 15px;
          margin-bottom: 5px;
          user-select: none;
        }
        
        .rdw-inline-wrapper {
          display: flex;
          align-items: center;
          margin-bottom: 6px;
          flex-wrap: wrap;
        }
        
        .rdw-option-wrapper {
          border: 1px solid #F1F1F1;
          padding: 5px;
          min-width: 25px;
          height: 20px;
          border-radius: 2px;
          margin: 0 4px;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: pointer;
          background: white;
          text-transform: capitalize;
        }
        
        textarea {
          width: 100%;
        }
        
        .save-btn {
          color: green;
        }
        
        .title__wrapper {
          margin: 0;
        }
        .App {
          text-align: center;
          width: 80%;
          margin: auto;
          color: gray;
        }
        
        .title {
          text-transform: uppercase;
          font-size: 2.2rem;
          text-align: center;
        
        }
        
        .page-title h2 {
          float: left;
        }
        `}
        </style>
      </div>
    )
  }
}

const mapStateToProps = ({ selectedArticle }) => ({ selectedArticle })

export default connect(mapStateToProps)(EditArticle1);